.circle {
  background-color: #ff6b08;
  border-radius: 50%;
}

.triangles {
  height: 25vmin;
  width: 25vmin;
  perspective: 15px;
  opacity: .8;
  animation: triangle-depth 3s cubic-bezier(.9, 0, .1, 1) infinite;
}

.triangle {
  --z: 0px;
  --size: 14%;
  height: 100%;
  width: 100%;
  mix-blend-mode: screen;
  transform: translateZ(var(--z) ) rotate(0turn);
  animation: triangle-spin 3s cubic-bezier(.9, 0, .1, 1) infinite;
  position: absolute;
}

.triangle.-other {
  --size: 7%;
  transform: translateZ(var(--z) ) rotate(60deg) rotate(0turn);
  animation-name: other-triangle-spin;
}

.dot {
  top: calc(50% - var(--size) / 2);
  left: calc(50% - var(--size) / 2);
  width: var(--size);
  height: var(--size);
  transform-style: preserve-3d;
  opacity: .8;
  --z: 45px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
}

.dot:nth-child(1) {
  transform: rotate(.333333turn) translateX(200%) translateZ(var(--z) );
}

.dot:nth-child(2) {
  transform: rotate(.666667turn) translateX(200%) translateZ(var(--z) );
}

.dot:nth-child(3) {
  transform: rotate(1turn) translateX(200%) translateZ(var(--z) );
}

.-big {
  --z: 6px;
}

.-big .dot {
  background: #000;
}

.-bigger {
  --z: 3px;
}

.-bigger .dot {
  z-index: 75;
  background: #ff001b;
}

@keyframes triangle-depth {
  0%, 100% {
    perspective: 14px;
    transform: scale(.8);
  }

  50% {
    perspective: 70px;
    transform: scale(1.3);
  }
}

@keyframes triangle-spin {
  100% {
    transform: translateZ(var(--z) ) rotate(1turn);
  }
}

@keyframes other-triangle-spin {
  100% {
    transform: translateZ(var(--z) ) rotate(60deg) rotate(1turn);
  }
}

